import React, { useState, useEffect } from 'react';
import ScrollView from 'react';
import { waitUntil } from 'async-wait-until';
import {
	IonContent, IonHeader, IonPage, IonTitle, IonToolbar, IonButton, IonCardHeader,
	IonCardContent, IonLabel, IonList, IonItem,IonTextarea,
	IonCard, useIonToast, IonGrid, IonRow, IonCol
} from '@ionic/react';

import { BleClient, numbersToDataView } from "@capacitor-community/bluetooth-le";
import ProgressBar from "@ramonak/react-progress-bar";
import './ota.css';
var CRC32 = require('crc-32');
var clientDevice: any;
var block_number = 0;
var expectedCmd = 0;
var notifiedCmd = 0;
var notifiedAns = 0;
var log_text = '';
const Ota: React.FC = () => {
	const [device, setDevice] = useState<string>('');
	const [log, setLog] = useState<string>('');
	const [scanned, setScaned] = useState(false);
	const [isConnected, setConnection] = useState(false);
	const [showBar, setProgressBar] = useState(false);
	const [otaFile, setOtaFile] = useState<ArrayBuffer>();
	const [progress, setProgress] = useState(0);
	const [product_code, setProductCode] = useState(0);
	const [product_model, setProductModel] = useState(0);
	const [product_version, setProductVersion] = useState(0);
	const [slot, setSlot] = useState(0);
	const [slot_num, setSlotNum] = useState(0);
	const [image_address, setImageAddress] = useState(0);
	const [image_max_size, setImageMaxSize] = useState(0);
	const [bch_size, setBCHSize] = useState(0);
	const [mcu_name, setMcuName] = useState<string>('');
	const [present] = useIonToast();
	const [btn_caption, setBtnCaption] = useState<string>('Disconnect');
	const [btn_color, setBtnColor] = useState<string>('danger');
	useEffect(() => {
		var element = document.getElementById("terminal");
		if(element != null  && log_text.length > 0)
			element.scrollTop = element.scrollHeight;
	  }, [log]);
	const setExpectedCmd = (exp:number) =>
	{
		expectedCmd = exp;
	}
	const setNotifiedCmd = (cmd:number) =>
	{
		notifiedCmd = cmd;
	}
	const setNotifiedAns = (ans:number) =>
	{
		notifiedAns = ans;
	}
	const onDisconnect = (deviceId: string) => {
		console.log(`device ${deviceId} disconnected`);
		log_text+=`Disconnected\n`;
		setLog(log_text);
		setConnection(false);
		setProgressBar(false);
		setBtnColor("primary");
		setBtnCaption("Connect");
	}
	const ab2str = (buf: ArrayBuffer) => {
		return new TextDecoder().decode(buf);
	};

	const presentToast = (position: 'top' | 'middle' | 'bottom', message: string) => {
		present({
			message: message,
			duration: 1500,
			position: position
		});
	};
	const handleNotification = (value: DataView) => {
		//console.log(value);
		if (value.getUint8(0) == 210) //OTA info answer
		{
			console.log("Product Code: " + value.getUint16(1));
			setProductCode(value.getUint16(1));
			console.log("Product Model: " + value.getUint16(3));
			setProductModel(value.getUint16(3));
			console.log("Version Number: " + value.getUint16(5));
			setProductVersion(value.getUint16(5));
			console.log("Slot: " + value.getUint8(7));
			setSlot(value.getUint8(7));
			console.log("Num Slot: " + value.getUint8(8));
			setSlotNum(value.getUint8(8));
			console.log("Image Address: " + value.getUint32(9));
			setImageAddress(value.getUint32(9));
			console.log("Image Max Size: " + value.getUint32(13));
			setImageMaxSize(value.getUint32(13));
			console.log("BCH Block Size: " + value.getUint32(17));
			setBCHSize(value.getUint32(17));
			var name = "";
			for (var i = 0; i < 16; i++) {
				name += String.fromCharCode(value.getUint8(17 + i));
			}
			name = name.replace(/[^a-zA-Z0-9]/g, '');
			setMcuName(name);
			console.log("MCU Name: " + name);
		}
		else {
			if((value.getUint8(0) == 212) && (value.byteLength == 4))
			{
				var blk = 0;
				blk = value.getUint8(3);
				blk*=256;
				blk += value.getUint8(2);
				block_number = blk;
			}
			setNotifiedCmd(value.getUint8(0));
			setNotifiedAns(value.getUint8(1));	
		}
	}
	const checkAck = () => {
		if (notifiedCmd == expectedCmd) {
			if (notifiedAns == 177) {
				//console.log("ACK");
				log_text+="ACK\n";
				setLog(log_text);
				return 1;
			}
		}
		return 0;
	}
	const WaitAck = async ():Promise<number> => {
		try {
			const result = await waitUntil(
				// Here, we specify a function that will be repeatedly called from time to time
				// Let's call this kind of function a `predicate`
				checkAck,
				// Here, we can specify a timeout in milliseconds. Once it passes, 
				// we'll stop waiting and throw an exception
				{ timeout: 5000, intervalBetweenAttempts:5 },
			);
			return 0;
		}
		catch (e) {
			console.log("error");
			return 1;
		}
	}
	const ScanClick = async () => {
		console.log("Scan Clicked");

		try {
			setConnection(false);
			setProgressBar(false);
			await BleClient.initialize();
			clientDevice = await BleClient.requestDevice({
				services: ['65333333-a115-11e2-9e9a-0800200ca100'],
				optionalServices: [],
			});
			setDevice(clientDevice.name as string);
			await BleClient.connect(clientDevice.deviceId, (deviceId) => onDisconnect(deviceId));
			console.log("Connected to device");
			log_text+="Connected to device\n";
			setLog(log_text);
			await BleClient.startNotifications(
				clientDevice.deviceId,
				'65333333-a115-11e2-9e9a-0800200ca100',
				'65333333-a115-11e2-9e9a-0800200ca102',
				(value) => {
					handleNotification(value);
				}
			);

			await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', numbersToDataView([210, 0, 0]));
			setConnection(true);
			setBtnColor("danger");
			setBtnCaption("Disconnect");
			if (scanned == false)
				setScaned(true);
		}
		catch (e) {
			console.error(e);
		}
	};
	const DisconnectClick = async () => {
		setProgressBar(false);
		if (isConnected) {
			setConnection(false);
			BleClient.disconnect(clientDevice.deviceId);
		}
		else {
			await BleClient.connect(clientDevice.deviceId, (deviceId) => onDisconnect(deviceId));
			console.log("Connected to device");
			log_text+="Connected to device\n";
			setLog(log_text);
			await BleClient.startNotifications(
				clientDevice.deviceId,
				'65333333-a115-11e2-9e9a-0800200ca100',
				'65333333-a115-11e2-9e9a-0800200ca102',
				(value) => {
					handleNotification(value);
				}
			);
			await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', numbersToDataView([210, 0, 0]));
			setConnection(true);
			setBtnColor("danger");
			setBtnCaption("Disconnect");
		}
	};
	const ClearClick = () =>
	{
		log_text ="";
		setLog(log_text);
		
	}
	function delay(ms:number) {
		return new Promise((resolve) => {
		   setTimeout(resolve, ms);
		})
	 }

	const append_header = (header: number, buf: ArrayBuffer) => {
		var tmp = new Uint8Array(1 + buf.byteLength);
		tmp[0] = header;
		tmp.set(new Uint8Array(buf), 1);
		return tmp.buffer;
	};
	const OtaClick = async () => {
		let result = 1;
		if (otaFile == null)
			return;
		var erase_slot = 0;
		if (slot == 0)
			erase_slot = 1;
		//Erease Cmd
		console.log("Send ERASE command");
		log_text+="Send ERASE command\n";
		setLog(log_text);
		setExpectedCmd(211);
		setNotifiedAns(0);
		setNotifiedCmd(0);
		await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', numbersToDataView([211, 0, 0]));
		result = await WaitAck();
		if(result)
		{
			//TODO: manage error
			console.log("T0");
			log_text+= "Timeout T0\n";
			setLog(log_text);
		}
		//Erase slot
		console.log("Send Slot to Erase: "+ erase_slot);
		log_text+="Send Slot to Erase: "+ erase_slot +"\n";
		setLog(log_text);
		setExpectedCmd(211);
		setNotifiedAns(0);
		setNotifiedCmd(0);
		await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', numbersToDataView([211, erase_slot]));
		result = await WaitAck();
		if(result)
		{
			//TODO: manage error
			console.log("T1");
			log_text+= "Timeout T1\n";
			setLog(log_text);
		}
		//Load CMD
		console.log("Send Load Cmd");
		log_text+="Send LOAD command\n";
		setLog(log_text);
		setExpectedCmd(212);
		setNotifiedAns(0);
		setNotifiedCmd(0);
		await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', numbersToDataView([212, 0, 0]));
		result = await WaitAck();
		if(result)
		{
			//TODO: manage error
			console.log("T2");
			log_text+= "Timeout T2\n";
			setLog(log_text);
		}
		//Send Header
		console.log("Send File Header");
		log_text+="Send File Header\n";
		setLog(log_text);
		let uploaded_len = 0;
		let to_send = otaFile.slice(uploaded_len, uploaded_len + 52);
		let payload = append_header(212, to_send);
		setExpectedCmd(212);
		setNotifiedAns(0);
		setNotifiedCmd(0);
		await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', new DataView(payload));
		result = await WaitAck();
		if(result)
		{
			//TODO: manage error
			console.log("T3");
			log_text+= "Timeout T3\n";
			setLog(log_text);
		}
		uploaded_len+= 52;
		let p_status = 0;
		block_number = 0;
		setProgressBar(true);
		while(uploaded_len < otaFile.byteLength)
		{
			//Send Blocks Header
			//console.log("Send Block "+block_number+" Header");
			log_text+= "Send Block "+block_number+" Header\n";
			setLog(log_text);
			to_send = otaFile.slice(uploaded_len, uploaded_len + 23);
			payload = append_header(212, to_send);
			setExpectedCmd(212);
			setNotifiedAns(0);
			setNotifiedCmd(0);
			await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', new DataView(payload));
			result = await WaitAck();
			if(result)
			{
				//TODO: manage error
				console.log("T4");
				log_text+= "Timeout T4\n";
				setLog(log_text);
			}
			uploaded_len+= 23;
			//Send Block
			let block_len = 0;
			let packet_len = 127;
			//console.log("Send Block "+block_number);
			log_text+= "Send Block "+block_number+"\n";
			setLog(log_text);
			to_send = otaFile.slice(uploaded_len, uploaded_len + packet_len);
			payload = append_header(212, to_send);
			setExpectedCmd(212);
			setNotifiedAns(0);
			setNotifiedCmd(0);
			await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', new DataView(payload));
			uploaded_len+= packet_len;
			block_len += packet_len;
			while (block_len < 1001) {
				to_send = otaFile.slice(uploaded_len, uploaded_len + packet_len);
				//payload = append_header(212, to_send);
				await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', new DataView(to_send));
				block_len+=packet_len;
				uploaded_len+=packet_len;
				//await delay(5);
				//console.log("Packet Len: " + packet_len + " Block Len: " + block_len);
				if (1001-block_len < packet_len) {
					packet_len = 1001-block_len;
				}
			}
			result = await WaitAck();
			if(result)
			{
				//TODO: manage error
				console.log("T5");
				log_text+= "Timeout T5\n";
				setLog(log_text);
			}
			p_status = Math.round(100 * uploaded_len / otaFile.byteLength);
			setProgress(p_status);
		}
		var done_packet = new Uint8Array(24);
		done_packet[0] = 212;
		done_packet[1] = 180;
		done_packet[2] = 180;
		setExpectedCmd(212);
		setNotifiedAns(0);
		setNotifiedCmd(0);
		console.log("Send DONE command");
		log_text+= "Send DONE command\n";
		setLog(log_text);
		await BleClient.write(clientDevice.deviceId, '65333333-a115-11e2-9e9a-0800200ca100', '65333333-a115-11e2-9e9a-0800200ca101', new DataView(done_packet.buffer));
		result = await WaitAck();
		/*if(result)
		{
			//TODO: manage error
			console.log("T6");
			log_text+= "Timeout T6\n";
		}*/
		setConnection(false);
		setProgressBar(false);
		BleClient.disconnect(clientDevice.deviceId);
	};
	const onFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		if (!event.target.files) return;
		let sourceFile = event.target.files[0];
		const reader = new FileReader();
		reader.addEventListener('load', (event) => {
			setOtaFile(reader.result as ArrayBuffer);
			//console.log(reader.result);
		});
		reader.readAsArrayBuffer(sourceFile);
	};
	return (
		<IonPage>
			<IonHeader>
			{!isConnected &&<IonToolbar>
					<IonTitle><IonButton onClick={() => {
						ScanClick();
					}}>Scan</IonButton></IonTitle>
				</IonToolbar>}
			</IonHeader>
			{scanned && <IonContent class="ion-padding">
				<IonHeader collapse="condense">
					<IonToolbar>
						<IonTitle size="large">Tab 1</IonTitle>

					</IonToolbar>
				</IonHeader>
				<h1>{device}</h1>
				<IonCard>

					<IonCardHeader>
						<IonGrid>
							<IonRow>
								<IonCol>
									<IonList>
										<IonItem>
											<IonLabel>Product Code: {product_code}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>Product Model: {product_model}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>Version Number: {product_version}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>MCU Name: {mcu_name}</IonLabel>
										</IonItem>
									</IonList>
								</IonCol>
								<IonCol>
									<IonList>
										<IonItem>
											<IonLabel>Slot: {slot}/{slot_num}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>Image Address: {image_address}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>Image Max Size: {image_max_size}</IonLabel>
										</IonItem>
										<IonItem>
											<IonLabel>BCH Block Size: {bch_size}</IonLabel>
										</IonItem>
									</IonList>
								</IonCol>
							</IonRow>
						</IonGrid>



					</IonCardHeader>
					<IonCardContent>
						<IonGrid>
							<IonRow>
							</IonRow>
						</IonGrid>
						<IonGrid>
							<IonRow>
								<IonCol>{!showBar && <input type="file" onChange={onFileChange} />}</IonCol>
								<IonCol>{showBar && <ProgressBar completed={progress} labelAlignment="center" bgColor="#3880ff" />}{(otaFile != null) && isConnected && <IonButton onClick={() => { OtaClick(); }}>OTA</IonButton>}</IonCol>
								<IonCol><IonButton class="ion-float-right" onClick={() => { DisconnectClick(); }} color={btn_color}>{btn_caption}</IonButton></IonCol>
								
							</IonRow>
						</IonGrid>
					</IonCardContent>
				</IonCard>
				<IonTextarea readonly id = "terminal" className="custom-textarea">{log}</IonTextarea>
				<IonCol><IonButton class="ion-float-right" onClick={() => { ClearClick(); }}>Clear</IonButton></IonCol>
			</IonContent>}

		</IonPage>
	);
};

export default Ota;
